body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
    --bg-dark: #1A2F3A;

    --logo-height-resp:120px;
    --logo-height: 100px;
    --aside-width: 225px;
    --header-height: 100px;
    --menu-top-height: 70px;
    --footer-height: 40px;

    --shadow: 
        0 2px 23px 0 rgba(0, 0, 0, 0.1),
        0 2px 49px 0 rgba(0, 0, 0, 0.06);

    --shadow-menu: 
        2px 0 10px 0 rgba(0, 0, 0, 0.12),
        2px 0 15px 0 rgba(0, 0, 0, 0.09);
    }

    *{
        box-sizing: border-box;
        font-family: 'Montserrat', sans-serif;

    }

    /* Layout  em GRID */

    .app{
        margin : 0;
        display: grid;
        grid-template-columns: 225px 1fr;
        grid-template-columns: var(--aside-width) 1fr;
        grid-template-rows:
            100px 
            1fr
            40px;
        grid-template-rows:
            var(--header-height) 
            1fr
            var(--footer-height);
        grid-template-areas:
        "logo header"
        "menu content"
        "menu footer"; 
    height: 100vh;
    background-color: #F5f5f5;

 }

 aside.logo {
     grid-area: logo;
 }

 header.header {
     grid-area: header;
 }

 aside.menu-area{
     grid-area: menu;
 }

 main.content{
     grid-area: content;
 }

 footer.footer{
     grid-area: footer;
 }

@media(max-width:768px){
    .app{
        grid-template-rows:
            120px
            70px
            100px
            1fr
            40px;
        grid-template-rows:
            var(--logo-height-resp)
            var(--menu-top-height)
            var(--header-height)
            1fr
            var(--footer-height);
        grid-template-columns: 225px 1fr;
        grid-template-columns: var(--aside-width) 1fr;
        grid-template-areas: 
            "logo logo"
            "menu menu"
            "header header"
            "content content"
            "footer footer";
    }
}

@media (max-width:576){
    .app{
        grid-template-rows: 
            100px
            70px
            1fr
            40px;
        grid-template-rows: 
            var(--logo-height)
            var(--menu-top-height)
            1fr
            var(--footer-height);
        grid-template-columns: 1fr;
        grid-template-areas: 
        "logo"
        "menu"
        "content"
        "footer";
    }
}
aside.logo{
    background-color: var(--bg-dark);
    display: flex;
    justify-content: center;
    align-items: center;
}

aside.logo img{
    padding: 0px 15px;
    width: 100%;
    
}

@media (max-width:768px){
    aside.logo img{
        width: 300px;
    }
}
aside.menu-area{
    background-color: var(--bg-dark);
    box-shadow: var(--shadow-menu);
}



.menu a {
    display:block;
    text-decoration: none;
    color: #fff;
    font-weight: 300;
    padding: 15px;
    
    
}

.menu a:hover{
    background: linear-gradient(135deg, #07a7e3 0%, #32dac3 100%);
}

@media (max-width: 768px) {
    .menu{
        display:flex;
        justify-content: flex-start;
        align-items: center;
    }

    .menu a{
        display:inline;
        padding: 10px;
        margin: 0px;
    }

    .menu{
        height: 100%;
        justify-content:space-around;
    }
}

main> div {
    background-color: #fff;
    box-shadow: 0px 0px 15px #0004;
}
header.header{
    background-color: #fff;
    padding: 0px 15px;
    overflow: hidden;
    white-space: nowrap;
    box-shadow: var(--shadow);
}

header.header h1{
    font-size: 1.8em;
}
footer.footer{
    display:flex;
    align-items:center;
    justify-content:flex-end;
    padding: 0 25px;
    background-color: #fff;
    box-shadow: var(--shadow);
}
